import "core-js/modules/es.json.stringify.js";
import { forceSyncAll, initSync, syncProviders, syncStatus } from '@/store/sync';
import { ref, computed, defineComponent, nextTick } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { list, get, del } from '@/store/impl';
import { last } from '@/store/impl/localStorage';
import dayjs from 'dayjs';
import { ElMessageBox } from 'element-plus';
import { disableAutoSave } from '@/store';
import providerInstallerList from '@/store/syncInstaller';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faPlus, faPenToSquare, faTrashCan } from '@fortawesome/free-solid-svg-icons';
library.add(faPlus, faPenToSquare, faTrashCan);
export default defineComponent({
  setup() {
    const delprovider = row => {
      syncProviders.value.splice(syncProviders.value.indexOf(row), 1);
      localStorage.removeItem('cocogoat.sync.v1.' + row.id);
    };

    const route = useRoute();
    const router = useRouter();
    const addType = computed(() => {
      return (route.params.app || '').toString();
    });
    const addComp = computed(() => {
      var _providerInstallerLis;

      return (_providerInstallerLis = providerInstallerList.find(i => i.class === addType.value)) === null || _providerInstallerLis === void 0 ? void 0 : _providerInstallerLis.component;
    });

    const addSave = ({
      key,
      data
    }) => {
      const ckey = key ? `${addType.value}_${key}` : new Date().getTime().toString(16).split('').reverse().join('').substring(0, 8);
      localStorage.setItem('cocogoat.sync.v1.' + ckey, JSON.stringify({
        class: addType.value,
        data
      }));
      router.replace({ ...route,
        params: {
          app: ''
        }
      });
      initSync().then(() => forceSyncAll(false));
    };

    const exportLoading = ref(false);

    const exportData = async () => {
      exportLoading.value = true;
      const Jszip = (await import('jszip')).default;
      const zip = new Jszip();
      const keys = list().concat(['options', 'currentUser']);
      keys.forEach(key => {
        const value = get(key);
        const lastModified = last(key).toISOString();
        zip.file(key + '.json', JSON.stringify({
          value,
          lastModified
        }, null, 4));
      });
      const blob = await zip.generateAsync({
        type: 'blob'
      });
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = `cocogoat-export-${dayjs().format('YYMMDDHHmm')}.zip`;
      a.click();
      exportLoading.value = false;
    };

    const clearData = async () => {
      try {
        await ElMessageBox.confirm('真的要清除本地数据吗？云端数据将依然保留，如需清理云端数据请通过删除账号方式操作。', '提示');
      } catch (e) {
        return;
      }

      disableAutoSave.value = true;
      await nextTick();
      const keys = list().concat(['options', 'currentUser']);
      keys.forEach(key => {
        del(key, false);
      });
      location.reload();
    };

    const errtype = {
      conflict: '同步冲突',
      offline: '网络错误',
      auth: '登录失效',
      disabled: '暂停服务'
    };
    return {
      syncProviders,
      syncStatus,
      providerInstallerList,
      delprovider,
      addType,
      addSave,
      addComp,
      forceSyncAll,
      clearData,
      exportData,
      exportLoading,
      errtype
    };
  }

});