import "core-js/modules/es.json.stringify.js";
import IconCocogoat from '@/components/Icons/cocogoat.vue';
import { apibase } from '@/utils/apibase';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faAngleRight, faEnvelopeOpenText, faCheckCircle, faCircleNotch, faBomb } from '@fortawesome/free-solid-svg-icons';
import { ElMessageBox } from 'element-plus';
import { computed, defineComponent, onBeforeUnmount, onMounted, ref } from 'vue';
library.add(faAngleRight, faEnvelopeOpenText, faCheckCircle, faCircleNotch, faBomb);
export const name = '椰羊云同步';
export default defineComponent({
  components: {
    IconCocogoat
  },
  emits: ['submit'],

  setup(props, {
    emit
  }) {
    let gtObj;

    let gtOnSucc = () => {// placeholder
    };

    const step = ref(1);
    onMounted(() => {
      window.initGeetest4({
        captchaId: '2c9c8579b5bc9d3771a03e7939ac0c66',
        product: 'bind'
      }, function (captchaObj) {
        captchaObj.onReady(function () {
          gtObj = captchaObj;
        }).onSuccess(function () {
          gtOnSucc();

          gtOnSucc = () => {// placeholder
          };
        }).onClose(function () {
          step.value = 1;
        });
      });
    });
    let intervalId = 0;
    onBeforeUnmount(() => {
      intervalId && clearTimeout(intervalId);
    });
    let destroyed = false;
    onBeforeUnmount(() => {
      destroyed = true;
    });
    const mail = ref('');
    const isMail = computed(() => {
      // 正则判断是否邮件地址
      const mailRegex = /^\s*\w+(?:\.{0,1}[\w-]+)*@[a-zA-Z0-9]+(?:[-.][a-zA-Z0-9]+)*\.[a-zA-Z]+\s*$/;
      return mailRegex.test(mail.value);
    });
    const timer = ref(0);
    const dtserver = ref('');
    const dtlocal = ref('');
    let dtoken = '';

    const submitMail = async () => {
      if (!isMail.value) return;
      step.value = 2;
      let gtres = null;

      if (gtObj) {
        gtObj.showCaptcha();
        await new Promise(resolve => {
          gtOnSucc = resolve;
        });
        gtres = gtObj.getValidate();
      }

      const res = await fetch(await apibase('/v2/lumine'), {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          email: mail.value,
          gt: gtres
        })
      });

      if (!res.ok || res.headers.get('content-type') !== 'application/json') {
        step.value = 1;
        let errorText = await res.text();

        try {
          const data = JSON.parse(errorText);
          errorText = data.error || data.msg;
        } catch (e) {}

        ElMessageBox.alert(errorText, '出错了！');
        return;
      }

      const data = await res.json();
      dtoken = data.ticket;
      timer.value = 180;

      const dec = () => {
        timer.value--;

        if (timer.value > 0) {
          setTimeout(dec, 1000);
        }
      };

      setTimeout(dec, 1000);
      step.value = 3;
      intervalId = setTimeout(checkLoginStatus, 3000);
    };

    const checkLoginStatus = async () => {
      if (destroyed) return;

      try {
        const res = await fetch(await apibase('/v2/lumine/' + dtoken), {
          method: 'DELETE'
        });

        if (res.status === 404) {
          intervalId = setTimeout(checkLoginStatus, 2000);
        } else if (res.status === 200) {
          const data = await res.json();
          data.lastModified = Date.now();
          const key = mail.value.replace(/\./g, '_').replace(/@/g, '_').replace(/-/g, '_');
          emit('submit', {
            key,
            data
          });
        } else {
          step.value = 1;
          let errorText = await res.text();

          try {
            const data = JSON.parse(errorText);
            errorText = data.error || data.msg;
          } catch (e) {}

          ElMessageBox.alert(errorText, '出错了！');
        }
      } catch (e) {
        intervalId = setTimeout(checkLoginStatus, 2000);
      }
    };

    return {
      step,
      mail,
      submitMail,
      timer,
      dtserver,
      dtlocal,
      isMail
    };
  }

});